.game{width:90vw;background-color:rgba(9,9,34,0.66);border-radius:5px;padding:5px 10px;max-height:760px;-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s}.game .fadeOut{visibility:hidden;opacity:0;-webkit-transition:visibility 0.2s linear 300ms, opacity 300ms;transition:visibility 0.2s linear 300ms, opacity 300ms}.game .fadeIn{visibility:visible;opacity:1;-webkit-transition:visibility 0.2s linear 0s, opacity 300ms;transition:visibility 0.2s linear 0s, opacity 300ms}.game .profile-and-btn{display:flex;justify-content:space-around;align-items:center;-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s}@media screen and (max-width: 700px){.game .profile-and-btn{justify-content:center;align-items:center;flex-direction:column}}.game hr{border:1px dotted midnightblue}.game .text-area{min-height:50px;border:2px solid midnightblue;border-radius:5px;padding:10px;background-color:rgba(0,186,186,0.78);-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s;cursor:not-allowed}.game .text-area:hover{box-shadow:0px 0px 16px 6px gray}.game .time-area p.timer{margin:2.5px 5px;display:flex;justify-content:center;font-size:1.25rem;font-weight:700;color:rgba(198,158,131,0.91);text-shadow:2px 1px 4px #161616}.game .typing-area{display:flex;flex-direction:column;align-items:center}.game .typing-area .progress-bar{width:100px;margin:5px}@media screen and (max-width: 760px){.game .typing-area .progress-bar{width:75px;margin:3px}}.game .typing-area .type-field{width:400px;height:40px;padding:1.5px;border:1px inset midnightblue;-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s;font-size:1.75rem;border-radius:5px}.game .typing-area .type-field:hover{box-shadow:0px 0px 16px 6px gray}@media screen and (max-width: 760px){.game .typing-area .type-field{width:250px;height:25px;padding:1px;font-size:1.25rem}}nav.head-block{width:100%;height:7.5vh;display:flex;align-items:center;background-color:deepskyblue;border-bottom:0.5px solid rgba(3,0,33,0.9)}nav.head-block ul{display:flex;width:74vw;margin:0;align-items:center;justify-content:flex-start}nav.head-block ul li{list-style:none;margin:auto 15px}nav.head-block ul li a{font-weight:bold;font-size:1.25rem;-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s;margin:0;padding:0;text-decoration:none}nav.head-block ul li a:hover{text-shadow:2px 1px 4px #161616;color:white;-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s}nav.head-block ul img{-webkit-transition:ease-out 0.2s;transition:ease-out 0.2s}nav.head-block ul img:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}nav.head-block ul button{margin:auto 15px;align-self:end}

